.flex {
  display: flex;
  align-items: center;
}
.dir-col {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-self: flex-start;
}

.bg-green {
  background-color: #51c3d8;
}

.feather {
  height: 25px;
  width: 25px;
  position: relative;
  left: 10px;
}
.logo {
  padding: 8px 8px;
}
.mr-1 {
  margin-right: 2px;
}

.nav {
  padding: 5px 30px;
  color: rgb(241, 241, 241);
  font-size: 14px;
}
.circle {
  height: 35px;
}
.space-between {
  justify-content: space-between;
}
.upgrade {
  border: 2px solid white;
  font-size: 12px;
  padding: 3px 13px;
  text-transform: uppercase;
  font-weight: 500;
  border-radius: 5px;
  margin-right: 30px;
  background-color: #81d6e6;
}
.lessthan {
  margin-right: 5px;
  height: 17px;
  position: relative;
  top: 4px;
}

.toolbar-class {
  background-color: rgb(236, 236, 236);
  padding: 10px 30px 3px 30px;
  display: flex;
  align-content: center;
  align-items: center;
}

.right {
  flex: 1;
}
.center {
  align-items: center;
}

.btn {
  font-size: 14px;
  padding: 3px 13px;
  text-transform: uppercase;
  font-weight: 500;
  border-radius: 5px;
  margin-right: 10px;
  text-decoration: none;
}
.grey {
  border: 2px solid grey;
  color: grey;
}

.green-btn {
  border: 2px solid #51c3d8;
  color: #51c3d8;
}

.fill-green {
  background-image: linear-gradient(to bottom right, #84ddeb, #2b7e8d);
  color: white;
  padding: 5px 13px;
}
.buttons {
  position: absolute;
  right: 20px;
  top: 65px;
}
.buttons > * {
  font-size: 10px;
}
.editor-class {
  margin: 60px 10vw;
  min-height: 90vh;
  box-shadow: 6px 0px 10px rgba(37, 41, 30, 0.04),
    0px -6px 10px rgba(0, 0, 0, 0.04), 0px 6px 10px rgba(0, 0, 0, 0.04),
    -6px 0px 10px rgba(0, 0, 0, 0.04);
  padding: 60px 80px;
}
.templates {
  font-size: 10px;
}
.template-img {
  padding: 2px 2px 0 2px;
}
.template {
  height: 17px;
}

.left-drop {
  width: 59.5px;
  height: 100vh;
  position: fixed;
  background-color: #ececec;
  left: 0;
  top: 0;
  z-index: -1;
}
.left {
  margin-top: 10px;
}

.inline {
  width: 130px;
  position: relative;
}

.dropdown {
  background-color: white;
  padding: 5px 10px;
  border-radius: 4px;
  color: rgb(107, 107, 107);
  border: none;
  margin-right: 10px;
  font-size: 10px;
}
.dropdown-btns {
  position: absolute;
  top: 70px;
  left: 105px;
}

.drop-icon {
  height: 13px;
}
.drop-white {
  height: 10px;
  margin-left: 5px;
}
/* .toolbar-class > * {
  display: none;
} */
.toolbar-class {
  min-height: 57px;
}
.highlighter-img {
  position: relative;
  height: 15px;
}

.styletext {
  min-width: 140px;
  text-align: left;
}

.styletext .drop-white {
  margin-left: 62px;
}
.font {
  min-width: 100px;
}
.font .drop-white {
  margin-left: 45px;
}

.normal .drop-white {
  margin-left: 15px;
}
.size,
.color-pick,
.highlight,
.quotes,
.align,
.bullets {
  padding-left: 5px;
  padding-right: 5px;
}
.show {
  display: block;
  opacity: 1;
  position: absolute;
}

.rdw-dropdown-carettoopen {
  box-sizing: border-box;
  height: 7px;
  width: 7px;
  border-style: solid;
  border-color: rgb(117, 117, 117);
  border-width: 0px 2px 2px 0px;
  transform: rotate(45deg);
  transition: 5px 150ms ease-in-out;
  position: absolute;
  right: 0;
}

.inline-o,
.normal-o,
.fontsize,
.font-o,
.color-picker-o,
.align-o,
.list-o {
  background-color: white;
  padding: 0px 8px;
  border-radius: 4px;
  color: rgb(83, 83, 83);
  border: none;
  margin-right: 10px;
  font-size: 9px;
  position: relative;
  font-weight: 500;
}
.inline-o {
  width: 120px;
  margin-top: -5px;
}
.fontsize {
  width: 40px;
  padding-left: 2px;
}
.color-picker-o img,
.list-o img,
.align-o img {
  opacity: 0;
}
.color-picker-o {
  height: 24px;
  width: 49px;
  padding: 22px 7px;
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-colour.svg");
  background-repeat: no-repeat;
  background-position: calc(100% - 20px) center;
  transform: scale(0.7);
  margin-left: -4px;
  border-radius: 5px;
}
.color-picker-o::after {
  content: "";
  box-sizing: border-box;
  height: 7px;
  width: 7px;
  border-style: solid;
  border-color: rgb(117, 117, 117);
  border-width: 0px 2px 2px 0px;
  transform: rotate(45deg);
  transition: 5px 150ms ease-in-out;
  position: absolute;
  right: 7px;
}
.rdw-dropdown-optionwrapper {
  width: 50px;
}

.list-o {
  height: 30px;
  width: 58px;
  padding: 22px 7px;
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-bullets.svg");
  background-repeat: no-repeat;
  background-position: calc(100% - 25px) center;
  transform: scale(0.7);
  margin-left: -10px;
  margin-top: -5px;
}
.align-o {
  height: 30px;
  width: 58px;
  padding: 22px 7px;
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-align-left.svg");
  background-repeat: no-repeat;
  background-position: calc(100% - 25px) center;
  transform: scale(0.7);
  margin-left: -10px;
  margin-top: -5px;
}

.list-o .rdw-dropdown-optionwrapper img,
.align-o .rdw-dropdown-optionwrapper img {
  display: block;
  height: 25px;
  opacity: 1;
}
.list-o .rdw-dropdown-optionwrapper {
  display: flex;
  width: 220px;
  height: 50px;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  position: relative;
  top: 28px;
}
.fontsize .rdw-dropdown-optionwrapper {
  width: 60px;
  padding-left: 2px;
}
.normal-o .rdw-dropdown-optionwrapper {
  width: 100px;
  padding-left: 2px;
  height: 150px;
}

.align-o .rdw-dropdown-optionwrapper {
  display: flex;
  width: 220px;
  height: 50px;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  position: relative;
  top: 28px;
  border: 0.5px solid rgba(128, 128, 128, 0.331);
  padding: 3px 10px;
}

.highlighter {
  width: 55px;
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-highlighter.svg");
}

.hide {
  display: none;
}

.inline-o .rdw-dropdown-optionwrapper {
  width: 150px;
  font-weight: 500;
}
.font-o .rdw-dropdown-optionwrapper {
  width: 150px;
  font-weight: 500;
}
.inline-o .rdw-dropdown-selectedtext img {
  opacity: 0;
}

.align-o ul li:nth-child(1) img {
  position: relative;
  opacity: 0;
}

.align-o ul li:nth-child(1) {
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-align-left.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.align-o ul li:nth-child(2) img {
  position: relative;
  opacity: 0;
}

.align-o ul li:nth-child(2) {
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-align-center.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.align-o ul li:nth-child(3) img {
  position: relative;
  opacity: 0;
}

.align-o ul li:nth-child(3) {
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon+feather-align-right.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.align-o ul li:nth-child(4) img {
  position: relative;
  opacity: 0;
}

.align-o ul li:nth-child(4) {
  background-image: url("https://my-backend-images.s3.ap-south-1.amazonaws.com/Icon-align-justify.svg");
  background-repeat: no-repeat;
  background-position: center;
}

